export const timeToNow = (time) => {
    const t = parseFloat(new Date - new Date(time)) / 1000;
    let str;
    if (t) {
        if (t > 60 && t < 3600) {
            str = `${parseInt(t / 60.0,10)}`
        } else if (t >= 3600 && t < 86400) {
            str = `${parseInt(t / 3600.0,10)}`
        } else if (t >= 86400 && t < 86400 * 30) {
            str = `${parseInt(t / 86400.0,10)}`
        } else if (t >= 86400 * 30 && t < 86400 * 365) {
            str = `${parseInt(t / (86400.0 * 30), 10)}`
        } else if (t >= 86400 * 365) {
            str = `${parseInt(t / (86400.0 * 365), 10)}`
        } else {
            str = `${parseInt(t, 10)}`
        }
    }
    return str
}

export const transTab = (tab) => {
    let str;
    switch (tab) {
        case 'good':
            str = ''
            break
        case 'share':
            str = ''
            break
        case 'job':
            str = ''
            break
        case 'ask':
            str = ''
            break
        default:
    }
    return str
}



