/**
 * 
 * @param  {Function} options.dispatch    store
 * @param  {String} options.topic_id    id
 * @param  {String} options.content     
 * @param  {String} options.accesstoken accesstoken
 * @param  {String} options.reply_id    id
 * @param  {Object} options.replyData   object
 * @return {Promise}                     Promise
 */
export const reply = ({
    dispatch
}, {
    topic_id,
    content,
    accesstoken,
    reply_id,
    replyData
}) => {
    const url = `/topic/${topic_id}/replies`;
    let params;
    if (reply_id) {
        params = {
            content,
            accesstoken,
            reply_id
        };
    } else {
        params = {
            content,
            accesstoken
        };
    }
    return _post(url, params)
        .then((json) => {
            if (json.success) {
                console.log('reply success');
                /* eslint-disable no-param-reassign */
                replyData.reply_id = json.reply_id;
                return replyData;
            }
            return Promise.reject(new Error('reply failure'));
        })
        .then((r) => dispatch('ADD_REPLIES', r))
        .catch((error) => {
            console.log(error);
            dispatch('REPLY_FAILURE');
            return Promise.reject();
        });
};

